    export default {
        props: {
            tabIndex: {
				type: Number,
				default: 0
			},
            duration: {
				type: Number,
				default: 300
			},
			tabBars: {
				type: Array,
				default () {
					return [];
				}
			},
        },
        computed: {
            
        },
        data() {
			return {
				
			}
		},
        methods: {
            async changeTab(e) {
                let index = e.target.current;
                this.tabIndex = index; //一旦访问data就会出问题
            },
			async tapTab(e) { //点击tab-bar
                if (this.tabIndex === e.target.dataset.current) {
                    return false;
                } else {
                    this.tabIndex = e.target.dataset.current
                }
            },
        }
    }
